<template>
  <!-- 我的收藏   专辑 -->
  <div class="albumlist">
    <!-- 头部 -->
    <div class="albumlist__head">
      <div class="albumlist__head__title">收藏的专辑 <span class="albumlist__head__title__count">({{ count }})</span></div>
      <div class="albumlist__head__search"></div>
    </div>
    <!-- 专辑列表 -->
    <div class="albumlist__list">
      <div v-for="(item, index) in AlbumList"
      :class="{albumlist__list__item: true, 'albumlist__list__item-dark': index%2==0}"
      :key="item.id">
            <img :src="item.picUrl" alt="" class="albumlist__list__item__pic">
            <span class="albumlist__list__item__item">{{ item.name }}</span>
          <div class="albumlist__list__item__singer">{{ item.artists[0].name}}</div>
          <div class="albumlist__list__item__size">{{ item.size }}首</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getAlbum } from '../../api/api_mycollect'
export default {
  name: 'AlbumList',
  data () {
    return {
      AlbumList: null,
      count: null
    }
  },
  methods: {
    async getData () {
      const res = await getAlbum()
      this.count = res.data.count
      this.AlbumList = res.data.data
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.albumlist{
  &__head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    &__title{
      font-size: 14px;
      font-weight: 500;
      &-count{
        font-size: 14px;
        color: #e1e1e1;
      }
    }
  }
  &__list{
      &__item{
        display: flex;
        align-items: center;
        padding: 10px 0;
        background-color: #ffffff;
          &__pic{
            height: 60px;
            width: 60px;
            border-radius: 5px;
          }
          &__item{
            flex: 1;
            font-size: 12px;
            height: 60px;
            line-height: 60px;
            color: #111111;
          }
        &__singer{
          width: 200px;
          font-size: 12px;
          color: #9c9c9c;
        }
        &__size{
          width: 90px;
          font-size: 12px;
          color: #9c9c9c;
        }
      }
      &__item-dark{
        background-color: #fafafa;
      }
      &__item:hover{
        background-color: #f1f2f3;
      }
    }
}
</style>
